#maindiv {
    width: 100%;
    margin-top: unset;
  }
#crudmednav{
    padding: 0% 3%;
    background: whitesmoke;
    box-shadow: 5px 5px 2px black;
    display: flex;
    justify-content: space-between;
    align-self: center;
  }
  #desdiv {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 20%;
  }
  #desdiv > i {
    color: maroon;
    font-size: 3rem;
  }
  #formdiv{
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      padding: 5%;
      width: 50%;
      margin: 5% auto;
      box-shadow:0 0 5px black;
      
    border-radius: 50px;
  }
  form{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 60vh;
    width: 80%;
  }
.stock1,.medName1,.mafName1,.price1,.discount1{
    width: 80%;
    height: 5vh;
    font-size: 1.5rem;
    border: none;
    outline: none;
    border-bottom: 3px solid black;
    background-color: transparent;
    }
  
  #buttonDiv{
    margin-top: 10%;
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      width: 70%;
  }
  .buttonClass4{
    font-size:15px;
    font-family:Arial;
    width:140px;
    height:50px;
    border-width:1px;
    color:#3a8a9e;
    border-color:#d6bcd6;
    font-weight:bold;
    border-top-left-radius:15px;
    border-top-right-radius:15px;
    border-bottom-left-radius:15px;
    border-bottom-right-radius:15px;
    box-shadow: 3px 4px 0px 0px #899599;
    text-shadow: 0px 1px 0px #e1e2ed;
    background:linear-gradient(#ededed, #bab1ba);

  padding:unset;
  }
  
  .buttonClass4:hover {
    background: linear-gradient(#bab1ba, #ededed);
  }