#maindiv{
    width: 100%;
    height: 100vh;
}
#navbar{
    padding:  0% 3%;
    background: whitesmoke;
    display: flex;
    justify-content: space-between;
    align-self: center;
    box-shadow: 2px 2px 10px black;
    border-bottom: 5px solid blue;
}
#desdiv{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 20%;
}
#desdiv > i{
    color: maroon;
    font-size: 3rem
}
#contentDiv{
    padding-top: 2%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    background-color: whitesmoke;
}
.ml{margin-left: 10px;}
#manageInventory,#manageOrders,#ManageTeam{
    box-shadow: 0px 0px 8px black;
    width:30%;
    height: 70vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    background-color: white;
    border-radius: 30px;

}
#inventory{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
}
#Team{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
}
#TeamDetails{
    padding-top: 5%;
    width: 70%;
    display: flex;
    flex-direction: column ;
    justify-content: space-evenly;
    align-items: center;
    box-shadow: 0px 0px 5px black;
    background-color: whitesmoke;
    font-size: 2.4rem;
    border-radius: 20px;
}
#orders{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}
#orderdetails{
    width: 80%;
    padding: 4%  6%;    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    box-shadow: 0px 0px 5px black;
    border-radius: 20px;
    background-color: whitesmoke;
}
.buttonClass {
    font-size:15px;
    font-family:Arial;
    width:140px;
    height:50px;
    border-width:1px;
    color:#3a8a9e;
    border-color:#d6bcd6;
    font-weight:bold;
    border-radius:15px;
    box-shadow: 3px 4px 0px 0px #899599;
    text-shadow: 0px 1px 0px #e1e2ed;
    background:linear-gradient(#ededed, #bab1ba);
    text-align: center;
    padding: unset;
  }
  
  .buttonClass:hover {
    background: linear-gradient(#bab1ba, #ededed);
  }
  #meddetails{
    width:40%;
    box-shadow: 0px 0px 8px black;
    background-color: whitesmoke;
    margin: 2% 0;
    text-align: center;
    border-radius: 30px;
  }