#maindiv {
  width: 100%;
  margin-top: unset;
}
#editTeamNav{
  padding: 0% 3%;
  background: whitesmoke;
  display: flex;
  justify-content: space-between;
  align-self: center;
  box-shadow: 5px 5px 10px black;
}
#desdiv {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 20%;
}
#desdiv > i {
  color: maroon;
  font-size: 3rem;
}
#teamImg > img {
  width: 100%;
  height: 50%;
}
#firstName {
  margin-right: 5%;
}
#contentArea {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
}
#TeamHolder {
  padding-top: 3%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  width: 80%;
  margin-top: 0 auto;
}

#TeamDetail {
  background: rgba(182, 243, 60, 0.384);
 
  box-shadow: 2px 2px 5px black;
  width: 33%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  margin: 3% auto;
  border-radius: 20px;
  overflow: hidden;
}
#memdetail {
text-align: left;
margin: 0 auto;
}
#TeamDetail:hover {
  background-color: blanchedalmond;
  box-shadow: 0 0 5px black;
}

.buttonClassaz {
  font-size: 15px;
  font-family: Arial;
  width: 140px;
  height: 50px;
  border-width: 1px;
  color: #3a8a9e;
  border-color: #d6bcd6;
  font-weight: bold;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  box-shadow: 3px 4px 0px 0px #899599;
  text-shadow: 0px 1px 0px #e1e2ed;
  background: linear-gradient(#ededed, #bab1ba);
  padding: unset;
}

.buttonClassaz:hover {
  background: linear-gradient(#bab1ba, #ededed);
}
.buttonClass2{
  font-size: 15px;
  font-family: Arial;
  width: 140px;
  height: 40px;
  border-width: 1px;
  color: #3a8a9e;
  border-color: #d6bcd6;
  font-weight: bold;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  box-shadow: 3px 4px 0px 0px #899599;
  text-shadow: 0px 1px 0px #e1e2ed;
  background: linear-gradient(#ededed, #bab1ba);
  padding: unset;

}

.buttonClass2:hover {
  background: linear-gradient(#bab1ba, #ededed);
}
#addMember {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 0 3%;
  box-shadow: 0px 0px 5px black;
  background-color: whitesmoke;
  z-index: 5;
  position: absolute;
  left: 65%;
  top: 30%;
  display: none;
  padding-bottom: 1%;
  padding-top: 2%;
  box-sizing: border-box;
}
.addform {
  border: none;
  outline: none;
  border-bottom: 2px solid black;
  background-color: transparent;
  padding-bottom: 2%;
  margin: 2% 0;
}
#btn2{
  margin-top: 10%;
}