}
#desdiv > i{
    color: maroon;
    font-size: 3rem
}
#contentArea{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction:column ;
}
#medHolder{
    padding-top: 3%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    width:80%;
    margin-top: 0 auto;
}
 
#itemDetail{
    background:whitesmoke;
    box-shadow: 0 0 5px black;
    padding: 2% 5%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    margin: 3% auto;
    border-radius:20px ;
    color: black;
}
#itemDetail:hover{
background-color: blanchedalmond;
border: 4px solid darkcyan;
}
#invNo{
    display: flex;
    width: 80%;
